import React from "react";

export const ProgressBar = props => {

  const numberStepsLeft = Math.abs(props.totPossibleProgress-props.currProgress);   
  const prog = (
    <div style={{zIndex: 2, position: 'relative'}}>
      
    <div style={{ display: "flex", justifyContent: "center", paddingBottom: '20px', flexWrap: 'wrap'}}>
      <h2 style={{ display: "flex", fontFamily: "cursive" }}> {props.progressLabel}: </h2>
      <div style={{ display: "flex", paddingLeft: '10px'}}>
        
        {[...Array(props.currProgress)].map((e, i) => (
          <div key={i}>
            <div
            style={{
              backgroundImage: "url("+props.progressImg+")",
              backgroundRepeat: "no-repeat",
              backgroundSize:"contain",
              backgroundPosition: "center",
              display: "flex",
              flex: "1 1 auto",
              paddingRight: '10px',
              paddingLeft: '10px', 
            }}
          > 
            
            <h4 style={{fontFamily: "cursive"}}>
                <span style={{backgroundColor: "rgb(74, 74, 74)", paddingLeft: '5px', paddinRight: '5px'}}>{props.stepLabels[i]}</span>
            </h4>
            

          </div>
          <h3 style={{fontFamily: "cursive"}}>
              {props.stepDates[i]}
          </h3>
        </div>))}
        
        {[...Array(numberStepsLeft)].map((e, k) => (
          <div key={k}>
              <div
            style={{
                backgroundImage: "url("+props.progressImg+")",
                backgroundRepeat: "no-repeat",
                backgroundSize:"contain",
                backgroundPosition: "center",
                display: "flex",
                flex: "1 1 auto",
                opacity: "0.4",
                paddingRight: '10px',
            }}> 
            <h4 style={{fontFamily: "cursive"}}>
                <span style={{backgroundColor: "rgb(74, 74, 74)", paddingLeft: '5px', paddingRight: '5px'}}>{props.stepLabels[k+props.currProgress]} </span>
            </h4>
        </div>
        <h3 style={{fontFamily: "cursive"}}>
        {props.stepDates[k+props.currProgress]}
         </h3></div>
        ))}
        </div></div></div>
    
  );
  return prog;
};
